import { UserService } from "./../../services/user.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import {
  LoadingController,
  ToastController,
  NavController,
} from "@ionic/angular";
import { Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-tempo-treino",
  templateUrl: "./tempo-treino.page.html",
  styleUrls: ["./tempo-treino.page.scss"],
})
export class TempoTreinoPage implements OnInit {
  form: FormGroup;
  constructor(
    private route: Router,
    private loadCtrl: LoadingController,
    private toastCtrl: ToastController,
    private navCtrl: NavController,
    private usrSrv: UserService
  ) {}

  ngOnInit() {
    this.initialForm();
  }

  initialForm() {
    this.form = new FormGroup({
      situacao: new FormControl(null, [Validators.required]),
      duracao: new FormControl(null, [Validators.required]),
      emocao: new FormControl(null, [Validators.required]),
    });
  }

  async submit() {
    const dados = this.form.value;

    if (!dados.situacao) {
      await this.showToast("Informe a situação do treino");
      return false;
    }

    this.usrSrv.insertTimeTrainig(dados).subscribe(
      async () => {
        await this.showToast("Tempo de treino inserido com sucesso");
        this.route.navigate(["menu-exercicios"]);
      },
      async (error) => {
        await this.showToast(error);
      }
    );

    console.log(this.form.value);
  }

  back() {
    this.navCtrl.back();
  }

  async showToast(message: string) {
    const toast = await this.toastCtrl.create({
      message,
      duration: 500,
    });

    await toast.present();
  }
}
